.screen-wrap {
  height: 100vh;
  width: 100vw;
  font-family: 'Montserrat', sans-serif;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-around;
  overflow: hidden;
}

::selection {
  background: yellow; /* WebKit/Blink Browsers */
}

h1 {
  font-size: 20px;
  z-index: 10;
  line-height: 25px;
  max-width: 400px;
}

.home-wrap h1 {
  cursor: default;
  background: transparent;
  position: relative;
  padding: 0;
  font-size: 35px;
  line-height: 40px;
  max-width: 1000px;
  text-align: center;
}

.home-wrap h1 > span {
  display: inline-block;
  padding: 8px 20px;
  background: linear-gradient(45deg, black 50%, black 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
}

.home-wrap h1:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(20deg, yellow 50%, transparent 50%);
  z-index: -1;
  color: black;
}

.home-wrap h1 > span,
.home-wrap h1:after {
  background-size: 300%;
  background-position: 100%;
  transition: 1s, font-size 0.2s;
  color: black;
}

.home-wrap h1:hover > span,
.home-wrap h1:hover:after {
  background-position: 0;
}

.avrender {
  z-index: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: .1;
  width: auto;
  height: auto;
  max-height: 900px;
}

.navigate {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  transition: .5s ease;
  z-index: 100;
}

.navigate:hover {
}

.nav-item {
  padding: 15px;
  cursor: pointer;
  transition: .5s ease;
  border-radius: 10px 10px 0 0;
}

.nav-item:hover {
  box-shadow: 0 0px 5px rgba(0,0,0,.5);
  background-color: rgba(255,255,255,.90);
}

.no-show {
  display: none;
}

.template-wrap {
  display: flex;
  height: 100vh;
  width: 100vw;
  /* max-width: 1500px; */
}

.template-content {
  display: flex;
  height: 100vh;
  width: 100vw;

}



.template-picture {
  height: 100vh;
  width: calc(100% - 560px);
  /* background-size: cover;
  background-position: center;
  background-repeat: no-repeat; */
  display: flex;
  flex-direction: column;
  z-index: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

.template-picture img {
  /* min-width: 100%;
  min-height: 100%; */
  width: 100%;
  /* position: fixed; */
}

.picture-wrap {
  margin-bottom: 53px;
  
}

.picture-wrap h4 {
  text-align: right;
  padding: 7px;
  font-size: 12px;
  background-color: black;
  color: white;
}

.template-text {
  width: 500px;
  position: fixed;
  right: 0;
  padding: 30px;
  top: 0;
  height: calc(100vh - 60px);
  max-height: 100vh;
  background-color: rgba(255,255,255,1);
  z-index: 10;
  overflow-y: scroll;
  overflow-x: hidden;
}

.template-text h1 {
  font-weight: bold;
  font-size: 53px;
  line-height: 67px;
  max-width: 500px;
  margin-bottom: 10px;
}

.template-text h2 {
  margin-bottom: 20px;
}

.template-text h2 span {
  font-style: normal;
}

.template-text h3 {
  font-weight: bolder;
  margin-bottom: 10px;
  margin-top: 30px;
}

.template-text p {
  line-height: 25px;
  margin-bottom: 15px;
  padding-left: 20px;
}

.template-text li {
  line-height: 25px;
  border-left: 2px solid black;
  padding-left: 15px;
}



.icon-box {
  display: flex;
  margin-bottom: 60px;
}

.icon-box img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.about-wrap {
  display: flex;
  max-width: 1000px;
  align-items: center;
}

.about-wrap img {
  max-height: 300px;
}

.resume-section {
  margin-bottom: 60px;
}


.resume-headshot {
  margin-right: 20px;
  cursor: pointer;
}

.resume-headshot {
  overflow: hidden;
  border-radius: 300px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 300px;
  align-items: center;
}

.resume-text {
  margin-left: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 600px;
}

.resume-text h3 .resume-email {
}

.resume-text h1 {
  font-size: 25px;
  line-height: 35px;
  margin-bottom: 20px;
  margin-right: 0;
}

.resume-text p {
  margin-bottom: 5px;
}

.resume-text h3 {
  font-size: 20px;
  line-height: 30px;
}
.resume-header {
  color: white;
  background-color: black;
  font-size: 12px;
  text-align: right;
  position: fixed;
  width: 100%;
  padding: 5px;
  line-height: 12px;
}

.resume-chunk {
  padding-left: 20px;
  margin-bottom: 20px;
  margin-left: 10px;
}

.resume-chunk h3 {
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: bold;
}

.resume-chunk h4 {
  font-size: 20px;
  font-style: italic;
  margin-bottom: 5px;
}

.resume-chunk li {
  line-height: 23px;
}

.resume-chunk ul {
  list-style-type: square;
  padding-left: 20px;
}